import React, { useState, useEffect } from "react";
import { Card, CardContent } from "../components/ui/card";
import { Button } from "../components/ui/button";
import { Line } from "react-chartjs-2";
import Scoreboard from "./Scoreboard";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from "chart.js";
import annotationPlugin from "chartjs-plugin-annotation";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
  annotationPlugin
);

const DailyScreen = () => {
  const [selectedDate, setSelectedDate] = useState(
    new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString().split("T")[0]
  );
  const [cgmData, setCgmData] = useState(new Array(96).fill(null));
  const [bolusData, setBolusData] = useState(new Array(96).fill(null));
  const [bolusDetails, setBolusDetails] = useState([]);
  const [scoreData, setScoreData] = useState({
    totalPoints: 0,
    pointsEarnedToday: 0,
    pointsDeductedToday: 0,
    negativeEvents: 0,
    positiveEvents: 0,
  });

  const fetchData = (date) => {
    fetch(`http://localhost:3000/api/cgm?date=${date}`)
      .then((response) => response.json())
      .then((data) => {
        const formattedData = new Array(96).fill(null);

        data.forEach(entry => {
          const timestamp = new Date(Date.parse(entry["Timestamp"]));
          const index = timestamp.getHours() * 4 + Math.floor(timestamp.getMinutes() / 15);
          if (index >= 0 && index < 96) {
            formattedData[index] = entry["CGM Value"];
          }
        });
        setCgmData([...formattedData]);
      })
      .catch(error => console.error("Error fetching CGM data:", error));

    fetch(`http://localhost:3000/api/bolus?date=${date}`)
      .then((response) => response.json())
      .then((data) => {
        const formattedBolusData = new Array(96).fill(null);
        const bolusDetailsArray = [];

        data.forEach(entry => {
          const timestamp = new Date(Date.parse(entry["Timestamp"]));
          const index = timestamp.getHours() * 4 + Math.floor(timestamp.getMinutes() / 15);
          if (index >= 0 && index < 96) {
            formattedBolusData[index] = entry["Insulin Delivered"];
            bolusDetailsArray[index] = {
              glucose: entry["Blood Glucose Input"],
              carbs: entry["Carbs Input"],
              ratio: entry["Carbs Ratio"],
              insulin: entry["Insulin Delivered"]
            };
          }
        });
        setBolusData([...formattedBolusData]);
        setBolusDetails(bolusDetailsArray);
      })
      .catch(error => console.error("Error fetching Bolus data:", error));

    fetch(`http://localhost:3000/api/scores?date=${date}`)
      .then((response) => response.json())
      .then((data) => {
        setScoreData(data);
      })
      .catch(error => console.error("Error fetching score data:", error));
  };

  useEffect(() => {
    fetchData(selectedDate);
  }, [selectedDate]);

  const updateDate = (days) => {
    const newDate = new Date(selectedDate);
    newDate.setDate(newDate.getDate() + days);
    setSelectedDate(newDate.toISOString().split("T")[0]);
  };

  const chartData = {
    labels: Array.from({ length: 96 }, (_, i) => {
      const hour = Math.floor(i / 4);
      const minute = (i % 4) * 15;
      return `${hour}:${minute.toString().padStart(2, '0')}`;
    }),
    datasets: [
      {
        label: "CGM Glucose Levels",
        data: cgmData,
        borderColor: "rgb(75, 192, 192)",
        backgroundColor: "rgba(75, 192, 192, 0.2)",
        pointRadius: 4,
        pointBackgroundColor: "rgb(75, 192, 192)",
        fill: false,
      },
    ],
  };

  return (
    <div className="dailyChartHead">
      <Card>
        <CardContent>
          <h2 className="text-xl font-semibold">Daily Blood Glucose Data</h2>
          <div align="center">
            <Button onClick={() => updateDate(-1)}>← Back</Button>
            <input type="date" value={selectedDate} onChange={(e) => setSelectedDate(e.target.value)} className="border p-2 rounded" />
            <Button onClick={() => updateDate(1)}>Next →</Button>
          </div>
          <div style={{ height: "400px", width: "99%" }}>
            <Line data={chartData} />
          </div>
          <Scoreboard scoreData={scoreData} />
        </CardContent>
      </Card>
    </div>
  );
};

export default DailyScreen;
